.banner{
    height: 840px;
    background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner.png?raw=true);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    
    .slogan{
        color: $primary;
        font-size: $font-xl;
        width: 576px;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        span{
            font-size: $font-4;
            font-weight: bold;
        }
        margin-right: 21%;
    }
}
.h2{
    font-size: $font-3;
    font-weight: bold;
}
.work{
    background-color: $thirdColor;
    padding: 60px 0;
    color: $primary;
    text-align: center;
    .h2{
        margin-bottom: 48px;
    }
    ul{
        display: flex;
        justify-content: center;
        align-items: flex-start;//消除間隙

        .cart{
            width: 255px;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin-right: 30px;
            img{
                width: 100px;
                height: 100px;
                margin-bottom: 24px;
            }
            .h3{
                width: 100%;
                font-size: $font-ll;
                font-weight: bold;
                margin-bottom: 8px;
            
            }
            p{
                text-align: left;
                color: #000;
                font-size: $font-1;
            }
            
        }
    }
    
}

.classic{
    display: flex;
    flex-wrap: wrap;
    .h2{
        width: 100%;
        margin-bottom: 8px;
        color: $primary;
        font-weight: bold;
    }
    ul{
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        .cart{
            width: 100%;
            display: flex;
            .product{
                max-width: 635px;
                margin-top: 32px;
                margin-right: 32px;
                vertical-align:text-top;
                
            }
            .icon{
                display: flex;
                align-items: center;
                img{
                    width: 48px;
                    height: 48px;
                    margin-right: 10px;
                }
                
                p{
                    font-size: $font-3;
                    font-style: italic;
                    font-weight: bold;
                    color: $secondary;
                }
            }
        }
    }
    
    
}

.cart:nth-child(2){
    a{
        flex-direction: row-reverse;
        p{
            margin-right: 30px;
        }
    } 
}

.co-branding{
    background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/bg.png?raw=true);
    padding: 60px 0 80px 0;
    background-position: center;
    background-size: cover;
    color: #fff;
    .box{
        display: flex;
        color: $secondary;
        .doubleA{
            background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/product-8.png?raw=true);
            background-size: cover;
            width: 540px;
            height: 405px;
            background-position:50% 50% ;
        }
        .youth{
            background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/product-9.png?raw=true);
            width: 540px;
            height: 405px;
            background-position:50% 50% ;
        }
        a{
            width: 540px;
            height: 405px;
            font-size: $font-xxl;
            font-weight: bold;
            color: $secondary;
            background: rgba(255,255,255,0.5);
        }
        a:hover{
            background:rgba(255,255,255,0);
        }
    }


}

.recommoned{
    .h2{
        color: $primary;
        font-weight: bold;
    }
    ul{
        .cart{
            border: 1px solid #DEE2E6;
            width: 255px;
            height: 460px;
            img{
                width: 255px;
                height: 256px;
            }
            .message{
                height: 204px;
                .h3{
                    text-align: left;
                }
                .h4{
                    text-align: left;
                    font-size: $font-s;
                    color: #707070;
                }
            }
        }
    }
}

.contactUs{
    background: $thirdColor;
    form{
        width: 730px;
        margin: 0 auto;
        .h2{
            p{
                font-size: $font-l;
                text-align: left;
            }
        }
        .sit{
            input{
                width: 100%;
                height: 50px;
            }
        }
        .button{
            width: 350px;
            height: 54px;
            background: $secondary;
            color: $white;
            font-size: $font-1;
        }
    }
    
}